<script>
import LeadDataService from "../../../services/DataService";
import crmtimeline from "../../../components/Common/Modal/crmtimeline.vue";
import crmnote from "../../../components/Common/Modal/crmnote.vue";
import crmattachment from "../../../components/Common/Modal/crmattachment.vue";
import crmsendemail from "../../../components/Common/Modal/crmsendemail.vue";
import crmcontacttab from "../../../components/Common/Modal/crmcontacttab.vue";
import crmproduct from "../../../components/Common/Modal/crmproduct.vue";
import crmcall from "../../../components/Common/Modal/crmcall.vue";
import modalattachment from "../../../components/Common/Modal/modalattachment.vue";
import modaltask from "../../../components/Common/Modal/modaltask.vue";
import modalevent from "../../../components/Common/Modal/modalevent.vue";
import modalcall from "../../../components/Common/Modal/modalcall.vue";
import modalemail from "../../../components/Common/Modal/modalemail.vue";
import modalexistingcontact from "../../../components/Common/Modal/modalexistingcontact.vue";
import modalcontact from "../../../components/Common/Modal/modalcontact.vue";
import modalproduct from "../../../components/Common/Modal/modalproduct.vue";
import addadditionalowner from "../../../components/Common/Modal/addadditionalowner.vue";
import modalconvertlead from "../../../components/Common/Modal/modalconvertlead.vue";
export default {
  components: {
    crmtimeline,
    crmnote,
    crmattachment,
    crmsendemail,
    crmcontacttab,
    crmproduct,
    crmcall,
    modalattachment,
    modaltask,
    modalevent,
    modalcall,
    modalemail,
    modalexistingcontact,
    modalcontact,
    modalproduct,
    addadditionalowner,
    modalconvertlead,
  },
  data() {
    return {
      activeKey: 1,
      isNotes: false,
      isActivity: false,
      isAttachment: false,
      isSendEmail: false,
      isContact: false,
      isProduct: false,
      isOwner: false,
      isDetailPage: false,
      iscontactDetails: null,
      showExistingContactModal: false,
      showContactModal: false,
      showConvertLead: false,
      showProductModal: false,
      showAddTaskModal: false,
      showAddEventModal: false,
      showCallModal: false,
      sendEmailModal: false,
      showAttachmentModal: false,
      LeftViewData: [],
      IsShow: true,
      noteData: [],
      attachmentData: [],
      activitiesData: null,
      timelinedata: [],
      productData: null,
      callData: null,
      contactsData: [],
      contactsHeader: [],
      noRecordForTab: false,
      isShowNoteDialogue: null,
      dataToConvertLead: {},
      noteText: "",
      leadName: "--",
      Acctab: "--",
      SortBy: "",
      SortExp: "",
      PageNumber: 1,
      PageSize: 100,
      LeadStatus: "",
      EmailData: [],
      modalItem: null,
      modalTaskItem: null,
      modalFollowUpItem: null,
      modalCallItem: null,
      modalDeleteItem: null,
      isView: false,
      modal: null,
      defaultemailid: null,
      showAdditionalOwnerPopup: false,
      additionalLeadOwners: [],
      additionalLeadOwnerId: null,
      additionalOwnerPermission: [],
      chkpermission: false,
      isShowNotePopup: false,
      expirydate: "",
      Count: 0,
      clienFirstName: "",
      clientLastName: "",
      clientaddr1: "",
      clientaddr2: "",
      clientcity: "",
      clientstate: "",
      clientzip: "",
      clientcompany: "",
      userid: null,
      companyId: null,
      containername: null,
      email: null,
      EmailInfo: null,
      ApiURLEmail: `id=0&refId=${this.$route.params.id}&moduleName=${this.$route.params.moduleName}&subModuleCode=${this.$route.params.subModuleCode}`,
      ApiURL: `id=${this.$route.params.id}&refId=${this.$route.params.id}&moduleName=${this.$route.params.moduleName}&subModuleCode=${this.$route.params.subModuleCode}`,
      maxsize:
        "@TALYGEN.Models.ConfigurationRepository.GetConfigurationValueByKey(companyId, userid, 'CRM', 'CRMPerFileSize')",
      disallowedfiletypes:
        "@TALYGEN.Models.ConfigurationRepository.GetConfigurationValueByKey(companyId, userid, 'CRM', 'CRMPerFileSize'))",
      NoteListType: "list",
      editUrl: `/CRM/ManageLead/${this.$route.params.id}/YES/CRM/CRM_LEADS`,
      privilegeParams: null,
      isLoading:false
    };
  },
  created: function () {
    this.userid = this.GetUserInfo.ID;
    this.companyId = this.GetUserInfo.CompanyId;
    this.containername = this.GetUserInfo.ContainerName;
    this.privilegeParams = `controller=CRM&action=`;
    this.email = this.GetUserInfo.Email;
    this.FetchData();
  },
  updated: function () {
    var vm = this;
    setTimeout(function () {
      vm.setHeaderForSelectedTab();
    }, 500);
    $("#ancActivityExpandAllCollapseAll").click(function () {
      if ($(this).attr("show") == "0") 
    {$(this).attr("show", "1");
     $("#divconversationbox").find("#accordion").find('.collapse').addClass('show'); }
     else { 
        $(this).attr("show", "0"); 
     $("#divconversationbox").find("#accordion").find('.collapse').removeClass('show')
    }
    });
  },
  methods: {
    PageTourStart: function () {
     startTour(0);
    },
    DisableButton: function () {
      var vm = this;
      vm.isNotes = false;
      vm.isActivity = false;
      vm.isAttachment = false;
      vm.isContact = false;
      vm.isSendEmail = false;
      vm.isProduct = false;
      vm.isOwner = false;
    },
    CollapseExpand: function (IsShow) {
      //
      var vm = this;
      vm.IsShow = !IsShow;
    },
    openShowNotePopup: function () {
      var ths = this;
      if (
        typeof ths.checkForAdditionalPermission !== "undefined" &&
        !ths.checkForAdditionalPermission("AddNote")
      ) {
        this.ShowAlert(
          this.$t("additionalOwnerAuthorityWarning"),
          "warning",
          true,
          this.$t("Alert")
        );
      } else {
        ths.isShowNotePopup = true;
        $("#tab-notes").trigger("click");
        ths.loadData("notes");
      }
    },
    checkPrivilegeDetailsForOwner: function (e, item) {
      var vm= this;
  vm.openAddAdditionalOwner(null);    
    },
    checkForAdditionalPermission: function (TypeCode) {
      var rtrn = true;
      if (
        this.additionalOwnerPermission != null &&
        this.additionalOwnerPermission.length > 0
      ) {
        var prmsn = this.additionalOwnerPermission.filter(function (c) {
          return c.permission_code == TypeCode;
        });
        if (prmsn.length == 0) {
          rtrn = false;
        }
      }
      return rtrn;
    },

    openAddAdditionalOwner: function (leaOwner) {
      var type = "AddAdditionalOwner";
      if (leaOwner != null) {
        this.additionalLeadOwnerId = leaOwner;
        type = "UpdateAdditionalOwner";
      } else {
        this.additionalLeadOwnerId = null;
      }
      if (
        typeof this.checkForAdditionalPermission !== "undefined" &&
        !this.checkForAdditionalPermission(type)
      ) {
        this.ShowAlert(
          this.$t("additionalOwnerAuthorityWarning"),
          "warning",
          true,
          this.$t("Alert")
        );
      } else {
        this.showAdditionalOwnerPopup =
        !this.showAdditionalOwnerPopup ? true : false;
      }
    },
    deleteAdditionalOwner: function (LeadOwnerId) {
      if (
        typeof this.checkForAdditionalPermission !== "undefined" &&
        !this.checkForAdditionalPermission("DeleteAdditionalOwner")
      ) {
        alert(
          this.$t("additionalOwnerAuthorityWarning"),
          "warning",
          true,
          this.$t("Alert")
        );
      } else {
        var vm = this;
        vm.confirmR(vm.$t("ConfirmDelete"), true, null, function () {
          var postJSON = {
            ids: LeadOwnerId,
            moduleName: "CRM",
            subModuleCode: "CRM_LEADS",
            refCode: "DELETE_VIEW_LEAD_OWNER",
            refId: vm.$route.params.id,
          };
          var objectPostString = JSON.stringify(postJSON);
          LeadDataService.CommonDelete(objectPostString).then(
            function (response) {
             if (response.data[0].Status == "Success") {
                vm.ShowAlert(
                  vm.$t("DeletedSuccess", [vm.$t("AdditionalOwner")]),

                  "success",
                  true,
                  vm.$t("Alert")
                );
                vm.loadData("additionalleadowners");
              } else {
                vm.ShowAlert(
                  vm.$t("DeletedError", [vm.$t("AdditionalOwner")]),

                  "failure",
                  true,
                  vm.$t("Alert")
                );
              }
            },
            function (response) {}
          );
        });
      }
    },
    ReopenLead: function () {
      var vm = this;
      if (vm.$route.params.id > 0) {
        vm.confirmR(
          this.$t("AreYouSuretoReopenLead"),
          true,
          null,
          function (result) {
            var postJSON = {
              ids: `${vm.$route.params.id}`,
            };
            var objectPostString = JSON.stringify(postJSON);
            LeadDataService.reopenLead(objectPostString).then(function (
              response
            ) {
              var box = "";
              if (response.data[0].Status == "Success") {
                if (
                  response.data[0].Value != "CONVERTED_TO_CONTRACT" &&
                  response.data[0].Value != "CONVERTED_TO_DEAL"
                ) {
                  vm.FetchData();
               
                  box = vm.ShowAlert(
                    vm.$t("LeadReopenedSuccessfully"),
                    "success",
                    true,
                    vm.$t("Alert")
                  );
                } else {
              
                  box = vm.ShowAlert(
                    vm.$t("CantReopenDueToConverted"),
                    "warning",
                    true,
                    vm.$t("Alert")
                  );
                }
              } else {
                //box = talygen.alert(vm.$t(Globalize.localize('ReopenLeadError')));
                box = vm.ShowAlert(
                  vm.$t("ReopenLeadError"),
                  "failure",
                  true,
                  vm.$t("Alert")
                );
              }
              setTimeout(function () {
                box.modal("hide");
              }, 4000);
            });
          }
        );
      }
    },
    GetLocalizedValue: function (fieldName, displayName) {
      if (this.isBlank(displayName)) {
        var chk = this.$t(String(fieldName).toUpperCase());
        if (chk || /^\s*$/.test(chk)) {
          return chk;
        }
        chk = this.$t(String(fieldName));
        if (chk || /^\s*$/.test(chk)) {
          return chk;
        }
        return fieldName;
      } else {
        var chk = this.$t(String(displayName).toUpperCase());
        if (chk || /^\s*$/.test(chk)) {
          return chk;
        }
        chk = this.$t(String(displayName));
        if (chk || /^\s*$/.test(chk)) {
          return chk;
        }
        return displayName;
      }
    },
    formatDate: function (value) {
      if (value) {
        return moment(
          value,
          String(Globalize.culture().calendar.patterns.d).toUpperCase()
        ).format(String(Globalize.culture().calendar.patterns.d).toUpperCase());
      }
    },

    FilterDDLandNormalValue: function (item) {
      if (item && item.length > 0) {
        try {
          return JSON.parse(item)[0].name;
        } catch (ex) {
          return "";
        }
      } else {
        return "";
      }
    },
    FilterDDLValueForID: function (item) {
      if (item.length > 0) {
        try {
          if (typeof JSON.parse(item)[0] == "object") {
            return JSON.parse(item)[0].value;
          } else {
            return JSON.parse(item)[0];
          }
        } catch (ex) {
          return "";
        }
      } else {
        return "";
      }
    },
    FetchData: function () {
      var vm = this;
      vm.isLoading=true;
      LeadDataService.FormLayoutView(vm.ApiURL).then(function (response) {
        var fullLeftJSON = response.data.LEFTDATA;
        vm.isLoading=false;
        vm.timelinedata = [];
        fullLeftJSON = $.grep(fullLeftJSON, function (element, index) {
          if (element.name == "role_title_id") {
            vm.dataToConvertLead.roletitle =
              JSON.parse(element.value) == null
                ? ""
                : JSON.parse(element.value)[0].value;
          }
          if (element.name == "home_phone") {
            vm.dataToConvertLead.home_phone = element.value;
          }
          if (element.name == "business_phone") {
            vm.dataToConvertLead.business_phone = element.value;
          }
          if (element.name == "contact_number") {
            vm.dataToConvertLead.contact_number = element.value;
          }
          if (element.name == "account_id") {
            vm.dataToConvertLead.account_id = element.value;
          }
          if (element.name == "channel_id") {
            vm.dataToConvertLead.lead_source_id =
              JSON.parse(element.value) == null
                ? ""
                : JSON.parse(element.value)[0].value;
          }
          if (element.name == "industry_type_id") {
            vm.dataToConvertLead.industry_type_id =
              JSON.parse(element.value) == null
                ? ""
                : JSON.parse(element.value)[0].value;
          }
          if (element.name == "lead_country") {
            vm.dataToConvertLead.lead_country =
              JSON.parse(element.value) == null
                ? ""
                : JSON.parse(element.value)[0].value;
          }
          if (element.name == "lead_state") {
            vm.dataToConvertLead.lead_state =
              JSON.parse(element.value) == null
                ? ""
                : JSON.parse(element.value)[0].value;
          }
          if (element.name == "external_source_id") {
            vm.dataToConvertLead.referred_by =
              JSON.parse(element.value) == null
                ? ""
                : JSON.parse(element.value)[0].value;
          } else if (element.name == "status_id") {
            vm.LeadStatus = vm.FilterDDLandNormalValue(element.value);
          } else if (element.name == "client_first_name") {
            vm.clienFirstName = element.value != null ? element.value : "";
          } else if (element.name == "client_last_name") {
            vm.clientLastName = element.value != null ? element.value : "";
          } else if (element.name == "lead_address1") {
            vm.clientaddr1 = element.value != null ? element.value : "";
          } else if (element.name == "lead_address2") {
            vm.clientaddr2 = element.value != null ? element.value : "";
          } else if (element.name == "lead_city") {
            vm.clientcity = element.value != null ? element.value : "";
          } else if (element.name == "lead_state") {
            vm.clientstate =
              element.value != null ? JSON.parse(element.value)[0].name : "";
          } else if (element.name == "lead_zipcode") {
            vm.clientzip = element.value != null ? element.value : "";
          } else if (element.name == "lead_company_name") {
            vm.clientcompany = element.value != null ? element.value : "";
          } else if (element.name == "client_email") {
            vm.defaultemailid = element.value != null ? element.value : "";
          } else if (element.name == "expected_close_date") {
            vm.dataToConvertLead.closing_date = element.value;
            vm.expirydate = element.value;
          } else if (element.name == "money_value") {
            vm.dataToConvertLead.ammount = element.value;
          } else if (element.name == "campaign_id") {
            vm.dataToConvertLead.campaign_id =
              JSON.parse(element.value) == null
                ? ""
                : JSON.parse(element.value)[0].value;
          } else if (element.name == "lead_owner_id") {
            vm.dataToConvertLead.lead_owner = element.value;
          }
          if (
            element.name == "commission_type" &&
            $.trim(element.value) != ""
          ) {
            element.form_field_visibility = "YES";
            element.display_order = 20;
          }
          if (element.name == "agreement_type" && $.trim(element.value) != "") {
            element.form_field_visibility = "YES";
            element.display_order = 19;
          }
          return element.form_field_visibility == "YES";
        });
        if (vm.leadName == "--") {
          vm.leadName = vm.clienFirstName + " " + vm.clientLastName;
        }
        if (vm.Acctab == "--") {
          vm.Acctab =
             vm.clienFirstName 
            // " " +
            // // vm.clientLastName +
            // // " " +
            // // vm.clientcompany +
            // // " " +
            // // vm.clientaddr1 +
            // // " " +
            // // vm.clientaddr2 +
            // // " " +
            // // vm.clientcity +
            // // " " +
            // // (vm.clientstate == "Select" ? " " : vm.clientstate) +
            // // " " +
            // // vm.clientzip;
        }
        vm.dataToConvertLead.lead_name = vm.leadName;
        if (
          response.data.RIGHTDATA != null &&
          response.data.RIGHTDATA.length > 0
        ) {
          var fullJSON = response.data.RIGHTDATA;
          var gruopBy = "";
          $.each(
            fullJSON,
            $.proxy(function (index, item) { 
              gruopBy = vm.$options.filters.formatDate(item.Complete_datetime);
              var chkGroup = $.grep(vm.timelinedata, function (element, index) {
                return element.groupKey == gruopBy;
              });
              if (chkGroup.length == 0) {
                var returnedData = $.grep(fullJSON, function (element, index) {
                  return vm.$options.filters.formatDate(element.Complete_datetime) == gruopBy;
                });
                if (returnedData.length > 0) {
                  var objGroup = {
                    groupKey: gruopBy,
                    groupData: returnedData,
                  };
                  vm.timelinedata.push(objGroup);
                }
              }
            }, this)
          );
          vm.isLoading=false;
          vm.noRecordForTab = false;
        } else {
          vm.noRecordForTab = true;
        }
        if (fullLeftJSON != null && fullLeftJSON.length > 0) {
          var groupId = fullLeftJSON[0].group_id;
          $.each(
            fullLeftJSON,
            $.proxy(function (index, item) {
              var chkGroup = $.grep(vm.LeftViewData, function (element, index) {
                return element.group_id == groupId;
              });
              if (chkGroup.length == 0) {
                var returnedData = $.grep(
                  fullLeftJSON,
                  function (element, index) {
                    return element.group_id == groupId;
                  }
                );
                returnedData = returnedData.sort(function (a, b) {
                  var a1 = a.display_order,
                    b1 = b.display_order;
                  if (a1 == b1) return 0;
                  return a1 > b1 ? 1 : -1;
                });
                if (returnedData.length > 0) {
                  var objGroup = {
                    group_id: groupId,
                    group_name: returnedData[0].group_name,
                    LeftViewData: returnedData,
                  };
                  vm.LeftViewData.push(objGroup);
                }
              }
              groupId = item.group_id;
            }, this)
          );
        }
        vm.additionalOwnerPermission = response.data.PERMISSION_DATA;
        setTimeout(function () {
          vm.CheckBoxBootstrap();
        }, 500);
        setTimeout(function () {
          vm.SetResponsiveTab();
        }, 500);
        setTimeout(function () {
          vm.RadioButtonBootstrap();
        }, 500);
        vm.isDetailPage = true;
      });
    },
    changeDateForAddNoteUsrEnd: function (value) {
      try {
        var timeFormat = Globalize.culture().calendar.patterns.t;
        timeFormat = String(timeFormat).replace("t", "A").replace("t", "");
        var getDate = new Date(
          moment(String(value)).year(),
          moment(String(value)).month(),
          moment(String(value)).date(),
          moment(String(value)).hours(),
          moment(String(value)).minutes()
        );
        if (userTimeZone[0].indexOf("+") >= 0) {
          getDate = new Date(
            getDate.setHours(
              getDate.getHours() - parseInt(userTimeZone[0].replace("+", ""))
            )
          );
          getDate = new Date(
            getDate.setMinutes(getDate.getMinutes() - parseInt(userTimeZone[1]))
          );
        } else if (userTimeZone[0].indexOf("-") >= 0) {
          getDate = new Date(
            getDate.setHours(
              getDate.getHours() + parseInt(userTimeZone[0].replace("-", ""))
            )
          );
          getDate = new Date(
            getDate.setMinutes(getDate.getMinutes() + parseInt(userTimeZone[1]))
          );
        }
        return moment(getDate, "YYYY-MM-DD HH:mm").format(
          String(Globalize.culture().calendar.patterns.d).toUpperCase() +
            " " +
            String(timeFormat)
        );
      } catch (e) {
        var timeFormat = Globalize.culture().calendar.patterns.t;
        timeFormat = String(timeFormat).replace("t", "a").replace("t", "");
        return moment(String(value)).format(
          String(Globalize.culture().calendar.patterns.d).toUpperCase() +
            " " +
            String(timeFormat)
        );
      }
    },
    DeleteActivityData: function (id) {
      var vm = this;
      if (id > 0) {
        vm.confirmR(vm.$t("ConfirmDelete"), true, null, function () {
          var postJSON = {
            ids: id,
            moduleName: "CRM",
            subModuleCode: "CRM_LEADS",
            refCode: "DELETE_VIEW_ACTIVITY",
            refId: vm.sourceId,
          };
          var objectPostString = JSON.stringify(postJSON);
          LeadDataService.CommonDelete(objectPostString).then(function (
            response
          ) {
           if (response.data[0].Status == "Success") {
              vm.ShowAlert(
                vm.$t("DeletedSuccess", [vm.$t("Activity")]),
                "Success",
                true,
                vm.$t("Alert")
              );
              vm.loadData("activities");
            } else {
              vm.ShowAlert(
                vm.$t("DeletedError", [vm.$t("Activity")]),
                "failure",
                true,
                vm.$t("Alert")
              );
            }
          });
        });
      }
    },
    openAttachmentDialogue: function () {
      this.isShowNoteDialogue = this.isShowNoteDialogue ? false : true;
    },
    openConvertLeadModel: function () {
      var vm = this;
      LeadDataService.CheckPrivilege(vm.privilegeParams + 'ConvertLead')
                .then(function (response) {
                  if (response.data) {
                    vm.confirmR(vm.$t("AreyousuretoConvertLead"), true, null, function () {
                      vm.showConvertLead = vm.showConvertLead ? false : true;
                    });
                  }
                  else {
                    vm.ShowAlert('You are not authorized', "failure", true, "Alert");
                  }

  });
    },
    openAddTaskModel: function () {
      if (
        typeof this.checkForAdditionalPermission !== "undefined" &&
        !this.checkForAdditionalPermission("AddAcitvities")
      ) {
        this.ShowAlert(
          this.$t("additionalOwnerAuthorityWarning"),
          "warning",
          true,
          this.$t("Alert")
        );
      } else {
        this.showAddTaskModal = this.showAddTaskModal ? false : true;
      }
    },
    openAddNewEvent: function () {
      if (
        typeof this.checkForAdditionalPermission !== "undefined" &&
        !this.checkForAdditionalPermission("AddAcitvities")
      ) {
        this.ShowAlert(
          this.$t("additionalOwnerAuthorityWarning"),
          "warning",
          true,
          this.$t("Alert")
        );
      } else {
        this.showAddEventModal = this.showAddEventModal ? false : true;
      }
    },
    openCallEvent: function () {
      if (
        typeof this.checkForAdditionalPermission !== "undefined" &&
        !this.checkForAdditionalPermission("AddAcitvities") 
      ) {
        this.ShowAlert(
          this.$t("additionalOwnerAuthorityWarning"),
          "warning",
          true,
          this.$t("Alert")
        );
      } else {
        this.showCallModal = this.showCallModal ? false : true;
      }
    },
    openEmailModal: function (item) {
      this.sendEmailModal = true;
      this.modalItem = item;
    },
    openActivityModal: function (item) {
      if (
        typeof this.checkForAdditionalPermission !== "undefined" &&
        !this.checkForAdditionalPermission("UpdateAcitvities")
      ) {
        this.ShowAlert(
          this.$t("additionalOwnerAuthorityWarning"),
          "warning",
          true,
          this.$t("Alert")
        );
      } else {
        if (item.activity_type == "Task") {
          this.showAddTaskModal = true;
          this.modalTaskItem = item;
        } else if (item.activity_type == "FollowUp") {
          this.showAddEventModal = true;
          this.modalFollowUpItem = item;
        } else if (item.activity_type == "Call") {
          this.showCallModal = true;
          this.modalCallItem = item;
        }
      }
    },
    DeleteActivity: function (item) {
      if (
        typeof this.checkForAdditionalPermission !== "undefined" &&
        !this.checkForAdditionalPermission("DeleteAcitvities")
      ) {
        this.ShowAlert(
          this.$t("additionalOwnerAuthorityWarning"),
          "warning",
          true,
          this.$t("Alert")
        );
      } else {
        this.modalDeleteItem = item;
        this.DeleteActivityData(this.modalDeleteItem.followup_notification_id);
      }
    },
    openCRMAttachmentModal: function () {
      if (
        typeof this.checkForAdditionalPermission !== "undefined" &&
        !this.checkForAdditionalPermission("AddAttachment")
      ) {
        alertR(
          Globalize.localize("additionalOwnerAuthorityWarning"),
          true,
          Globalize.localize("Alert")
        );
      } else {
        this.showAttachmentModal =
          this.showAttachmentModal ? false : true;
      }
    },
    loadData: function (tabName, callFrom) {
    
      var vm = this;
      vm.DisableButton();
      vm.isDetailPage = false;

      if (tabName == "notes") {
        vm.isNotes = true;
        vm.activeKey = 2;
        if (callFrom && callFrom == "savenote") vm.isShowNotePopup = false;
      } else if (tabName == "attachments") {
        vm.isAttachment = true;
        vm.activeKey = 3;
      } else if (tabName == "activities") {
        vm.activeKey = 4;
        vm.isActivity = true;
      } else if (tabName == "contacts") {
        vm.activeKey = 6;
        vm.isContact = true;
      } else if (tabName == "email") {
        vm.activeKey = 5;
        vm.isSendEmail = true;
      } else if (tabName == "products") {
        vm.activeKey = 7;
        vm.isProduct = true;
      } else if (tabName == "additionalleadowners") {
        vm.activeKey = 9;
        vm.isOwner = true;
      }
      if (tabName != "" && tabName != null) {
        if (tabName == "attachments") {
          LeadDataService.AttachmentListing(vm.ApiURL).then(function (
            response
          ) {
            if (response.data != null) {
              vm.attachmentData = response.data.data;
              setTimeout(function () {
                vm.CheckBoxBootstrap();
              }, 500);
              setTimeout(function () {
                vm.setHeaderForSelectedTab();
              }, 500);
              setTimeout(function () {
                vm.bindLeftTabEventForSetHeading();
              }, 500);
              vm.noRecordForTab = false;
            } else {
              setTimeout(function () {
                vm.setHeaderForSelectedTab();
              }, 500);
              setTimeout(function () {
                vm.bindLeftTabEventForSetHeading();
              }, 500);
              vm.noRecordForTab = true;
            }
          });
        } else if (tabName == "activities") {
          var vm= this;
          LeadDataService.CheckPrivilege(vm.privilegeParams + 'Upcoming_Activities')
                .then(function (response) {
                  if (response.data) {
                    LeadDataService.ActivityListing(vm.ApiURL).then(function (response) {
                      if (
                        response.data != "" &&
                        response.data != null &&
                        response.data.data.length > 0
                      ) {
                        vm.activitiesData = response.data.data;
                        setTimeout(function () {
                          vm.CheckBoxBootstrap();
                        }, 500);
                        setTimeout(function () {
                          vm.ResponsiveDataTable("tablelistingdata");
                        }, 500);
                        setTimeout(function () {
                          vm.setHeaderForSelectedTab();
                        }, 500);
                        setTimeout(function () {
                          vm.bindLeftTabEventForSetHeading();
                        }, 500);
                        vm.noRecordForTab = false;
                      } else {
                        setTimeout(function () {
                          vm.setHeaderForSelectedTab();
                        }, 500);
                        setTimeout(function () {
                          vm.bindLeftTabEventForSetHeading();
                        }, 500);
                        vm.noRecordForTab = true;
                      }

                    });
                  }
                  else {
                    vm.ShowAlert('You are not authorized', "failure", true, "Alert");
                  }


          });
        } else if (tabName == "timeline") {
          vm.activeKey = 1;
          vm.isDetailPage = true;
          LeadDataService.TimelineListing(vm.ApiURL).then(function (response) {
            if (response.data != null) {
              vm.timelinedata = [];
              var fullJSON = response.data.data;
              var gruopBy = "";
              $.each(
                fullJSON,
                $.proxy(function (index, item) {
                  gruopBy = vm.$options.filters.formatDate(item.Complete_datetime);
                  var chkGroup = $.grep(
                    vm.timelinedata,
                    function (element, index) {
                      return element.groupKey == gruopBy;
                    }
                  );
                  if (chkGroup.length == 0) {
                    var returnedData = $.grep(
                      fullJSON,
                      function (element, index) {
                        return vm.$options.filters.formatDate(element.Complete_datetime) == gruopBy;
                      }
                    );
                    if (returnedData.length > 0) {
                      var objGroup = {
                        groupKey: gruopBy,
                        groupData: returnedData,
                      };
                      vm.timelinedata.push(objGroup);
                    }
                  }
                }, this)
              );
              setTimeout(function () {
                vm.CheckBoxBootstrap();
              }, 500);
              setTimeout(function () {
                vm.setHeaderForSelectedTab();
              }, 500);
              setTimeout(function () {
                vm.bindLeftTabEventForSetHeading();
              }, 500);
              vm.noRecordForTab = false;
            } else {
              setTimeout(function () {
                vm.setHeaderForSelectedTab();
              }, 500);
              setTimeout(function () {
                vm.bindLeftTabEventForSetHeading();
              }, 500);
              vm.noRecordForTab = true;
            }
          });
        } else if (tabName == "products") {
          var url = `id=${this.$route.params.id}&moduleName=${this.$route.params.moduleName}&subModuleCode=${this.$route.params.subModuleCode}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}`;

          LeadDataService.getItemListForAssociate(url).then(function (
            response
          ) {
            if (response.data != null && response.data != "") {
              vm.productData = response.data.data;
              setTimeout(function () {
                vm.CheckBoxBootstrap();
              }, 500);
              setTimeout(function () {
                vm.setHeaderForSelectedTab();
              }, 500);
              setTimeout(function () {
                vm.bindLeftTabEventForSetHeading();
              }, 500);
              vm.noRecordForTab = false;
            } else {
              setTimeout(function () {
                vm.setHeaderForSelectedTab();
              }, 500);
              setTimeout(function () {
                vm.bindLeftTabEventForSetHeading();
              }, 500);
              vm.noRecordForTab = true;
              vm.productData = null;
            }
          });
        } else if (tabName == "calls") {
          vm.activeKey = 8;
          url = `id=${this.$route.params.id}&subModuleCode=${this.$route.params.subModuleCode}`;
          LeadDataService.GetCallLogsListinge(url).then(function (response) {
            if (response.data != null) {
              vm.callData = response.data.data;
              setTimeout(function () {
                vm.CheckBoxBootstrap();
              }, 500);
              setTimeout(function () {
                vm.setHeaderForSelectedTab();
              }, 500);
              setTimeout(function () {
                vm.bindLeftTabEventForSetHeading();
              }, 500);
              vm.noRecordForTab = false;
            } else {
              setTimeout(function () {
                vm.setHeaderForSelectedTab();
              }, 500);
              setTimeout(function () {
                vm.bindLeftTabEventForSetHeading();
              }, 500);
              vm.noRecordForTab = true;
              vm.calltData = null;
            }
          });
        } else if (tabName == "contacts") {
         
          var url = `id=${this.$route.params.id}&subModuleCode=${this.$route.params.subModuleCode}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}`;
          LeadDataService.ContactListing(url).then(function (response) {
            if (response.data != null) {
              vm.contactsData = response.data.data;
              vm.contactsHeader = response.data.schema;
              setTimeout(function () {
                vm.CheckBoxBootstrap();
              }, 500);
              setTimeout(function () {
                vm.setHeaderForSelectedTab();
              }, 500);
              setTimeout(function () {
                vm.bindLeftTabEventForSetHeading();
              }, 500);
              vm.noRecordForTab = false;
            } else {
              setTimeout(function () {
                vm.setHeaderForSelectedTab();
              }, 500);
              setTimeout(function () {
                vm.bindLeftTabEventForSetHeading();
              }, 500);
              vm.noRecordForTab = true;
              vm.contactsData = null;
            }
          });
        } else if (tabName == "email") {
          var url =
            vm.ApiURLEmail +
            vm.SortBy +
            "&sortExp=" +
            vm.SortExp +
            "&pageSize=" +
            vm.PageSize +
            "&pageNumber=" +
            vm.PageNumber;
          LeadDataService.EmailListing(url).then(function (response) {
            if (response.data != null) {
              vm.EmailData = response.data.data;
              setTimeout(function () {
                vm.CheckBoxBootstrap();
              }, 500);
              setTimeout(function () {
                vm.setHeaderForSelectedTab();
              }, 500);
              setTimeout(function () {
                vm.bindLeftTabEventForSetHeading();
              }, 500);
              vm.noRecordForTab = false;
            } else {
              setTimeout(function () {
                vm.setHeaderForSelectedTab();
              }, 500);
              setTimeout(function () {
                vm.bindLeftTabEventForSetHeading();
              }, 500);
              vm.noRecordForTab = true;
            }
          });
        } else if (tabName == "notes") {
          vm.noteData = [];
          LeadDataService.NoteListing(vm.ApiURL).then(function (response) {
            if (response.data != null && response.data.data.length > 0) {
              vm.noteData = response.data.data;
              setTimeout(function () {
                vm.CheckBoxBootstrap();
              }, 500);
              setTimeout(function () {
                vm.setHeaderForSelectedTab();
              }, 500);
              setTimeout(function () {
                vm.bindLeftTabEventForSetHeading();
              }, 500);
              vm.noRecordForTab = false;
            } else {
              setTimeout(function () {
                vm.setHeaderForSelectedTab();
              }, 500);
              setTimeout(function () {
                vm.bindLeftTabEventForSetHeading();
              }, 500);
              vm.noRecordForTab = true;
            }
          });
        } else if (tabName == "additionalleadowners") {
          var url = `id=${this.$route.params.id}&subModuleCode=CRM_LEADS`;
          LeadDataService.additionalLeadOwnerListing(url).then(function (
            response
          ) {
            if (response.data != null && response.data.DATA.length > 0) {
              vm.additionalLeadOwners = response.data.DATA;
              setTimeout(function () {
                vm.CheckBoxBootstrap();
              }, 500);
              setTimeout(function () {
                vm.ResponsiveDataTable("table-leadOwner-content");
              }, 500);
              setTimeout(function () {
                vm.setHeaderForSelectedTab();
              }, 500);
              setTimeout(function () {
                vm.bindLeftTabEventForSetHeading();
              }, 500);
              vm.noRecordForTab = false;
            } else {
              setTimeout(function () {
                vm.setHeaderForSelectedTab();
              }, 500);
              setTimeout(function () {
                vm.bindLeftTabEventForSetHeading();
              }, 500);
              vm.noRecordForTab = true;
            }
          });
        }
      }
    },

    ChangeCRMLeadOwnerCheck: function (event) {
      var varObj = this;
      $(".chkItems:checkbox:checked")
        .not("[id^='chkAll']")
        .each(function () {
          varObj.OwnerIds.push($(this).val());
        });

      if (this.OwnerIds.length > 0) {
        this.showLeadOwnerModal =
          this.showLeadOwnerModal ? false : true;
      } else {
        alert(varObj.$t("Selectchangeleadowner"));
      }
    },
    openCRMEmailModal: function (item) {
      var type = "AddEmail";
      if (item != null) {
        type = "UpdateEmail";
      }
      if (
        typeof this.checkForAdditionalPermission !== "undefined" &&
        !this.checkForAdditionalPermission(type) 
      ) {
        this.ShowAlert(this.$t("additionalOwnerAuthorityWarning"),"warning",true,this.$t("Alert"));
      } else {
        this.EmailInfo = item;
        this.sendEmailModal = this.sendEmailModal ? false : true;        
      }
    },
    openCRMProductModel: function () {
      if (
        typeof this.checkForAdditionalPermission !== "undefined" &&
        !this.checkForAdditionalPermission("AddProducts") 
      ) {
        this.ShowAlert(this.$t("additionalOwnerAuthorityWarning"),"warning",true,this.$t("Alert"));
      } else {
        this.showProductModal = this.showProductModal ? false : true;
      }
    },
    GetEmailInfo: function (item) {
      this.EmailInfo = item;
      this.sendEmailModal = this.sendEmailModal ? false : true;
    },
    openCRMExistingContactModel: function () {
      if (
        typeof this.checkForAdditionalPermission !== "undefined" &&
        !this.checkForAdditionalPermission("AddContact")
      ) {
        this.ShowAlert(this.$t("additionalOwnerAuthorityWarning"),"warning",true,this.$t("Alert"));
      } else {
        this.showExistingContactModal =
          this.showExistingContactModal ? false : true;
      }
    },
    openCRMContactModal: function (contactDetails) {
      var type = "AddContact";
      if (contactDetails != null) {
        type = "UpdateContact";
      }
      if (
        typeof this.checkForAdditionalPermission !== "undefined" &&
        !this.checkForAdditionalPermission(type)
      ) {
        this.ShowAlert(this.$t("additionalOwnerAuthorityWarning"),"warning",true,this.$t("Alert"));
      } else {
        this.iscontactDetails = contactDetails;
        this.showContactModal = this.showContactModal ? false : true;
      }
    },
    closeConvertLead(){
            this.showConvertLead = false;
            this.FetchData();
        },
  },
};
</script>
<template>
  <div id="divMainFormData" v-cloak>
   
    <section id="main-content" class="main-content">
      <loader :is-visible="isLoading"></loader>
      <div class="left-menu-tab div-click-event hr_jobpost_a">
        <div class="col-md-12 p-0">
          <div class="theme-primary partition">
            <span class="p-name text-white textelipsis">{{ $t('Lead') }}</span>
          </div>
        </div>
        <ul
          class="nav nav-pills flex-column left-tab mt-2 responivecustomtab"
          id="myTab"
          role="tablist"
        >
          <li class="nav-item">
            <a
              class="nav-link left-menu"
              :class="{ 'active': activeKey == 1 }"
