              href="javascript:void(0)"
              id="tab-notes"
              data-bs-toggle="tab"
              role="tab"
              v-on:click="loadData('notes')"
            >
              <em class="fa fa-file-text" aria-hidden="true" aria-selected="false"></em>
              {{ $t('Note') }}
            </a>
          </li>
          <li class="nav-item" v-if="checkForAdditionalPermission('ViewAttachment')">
            <a
              class="nav-link left-menu"
              :class="{ 'active': activeKey == 3 }"
              data-bs-target="#tab-attachment-content"
              href="javascript:void(0)"
              id="tab-attachment"
              data-bs-toggle="tab"
              role="tab"
              v-on:click="loadData('attachments')"
