              <em class="fa fa-file" aria-hidden="true" aria-selected="false"></em>
              {{ $t('Attachments') }}
            </a>
          </li>
          <li class="nav-item" v-if="checkForAdditionalPermission('ViewAcitvities')">
            <a
              class="nav-link left-menu"
              :class="{ 'active': activeKey == 4 }"
              data-bs-target="#tab-openactivities-content"
              href="javascript:void(0)"
              id="tab-openactivities"
              data-bs-toggle="tab"
              role="tab"
              v-on:click="loadData('activities')"
              aria-selected="false"
            >
              <em class="fa fa-calendar" aria-hidden="true"></em>
              {{ $t('Activities') }}
            </a>
          </li>
          <li class="nav-item" v-if="checkForAdditionalPermission('ViewEmail')">
            <a
              class="nav-link left-menu"
              :class="{ 'active': activeKey == 5 }"
              href="javascript:void(0)"
              id="tab-emails"
              data-bs-toggle="tab"
              role="tab"
              v-on:click="loadData('email')"
              aria-selected="false"
            >
              <em class="fa fa-envelope" aria-hidden="true"></em>
              {{ $t('Emails') }}
            </a>
          </li>
          <li class="nav-item" v-if="checkForAdditionalPermission('ViewContact')">
            <a
              class="nav-link left-menu"
              :class="{ 'active': activeKey == 6 }"
              href="javascript:void(0)"
              id="tab-Contact"
              data-bs-toggle="tab"
              role="tab"
              v-on:click="loadData('contacts')"
              aria-selected="false"
            >
              <em class="fa fa-users" aria-hidden="true"></em>
              {{ $t('Contacts') }}
            </a>
          </li>
          <li class="nav-item" v-if="checkForAdditionalPermission('ViewProducts')">
            <a
              class="nav-link left-menu"
              :class="{ 'active': activeKey == 7 }"
              href="javascript:void(0)"
              id="tab-products"
              data-bs-toggle="tab"
              role="tab"
              v-on:click="loadData('products')"
            >
              <em class="fa fa-shopping-cart" aria-hidden="true" aria-selected="false"></em>
              {{ $t('Products') }}
            </a>
          </li>
          <li class="nav-item">
            <a
              class="nav-link left-menu"
              :class="{ 'active': activeKey == 8 }"
              href="javascript:void(0)"
              id="tab-calls"
              data-bs-toggle="tab"
              role="tab"
              v-on:click="loadData('calls')"
            >
              <em class="fa fa-phone-square" aria-hidden="true" aria-selected="false"></em>
              {{ $t('Call') }} {{ $t('Details') }}
            </a>
          </li>
          <li class="nav-item" v-if="checkForAdditionalPermission('ViewAdditionalOwner')">
            <a
              class="nav-link left-menu"
              :class="{ 'active': activeKey == 9 }"
              href="javascript:void(0)"
              id="tab-CoOwner"
              data-bs-toggle="tab"
              role="tab"
              v-on:click="loadData('additionalleadowners')"
            >
              <em class="fa fa-user-plus" aria-hidden="true" aria-selected="false"></em>
              {{ $t('AdditionalOwners') }}
            </a>
          </li>
        </ul>
      </div>
      <div class="right-content hr_jobpost">
        <div class="col-md-12 p-0 mb-2">
          <div class="theme-primary partition p-actions-expand">
            <span class="p-name text-white" id="span_ForSelectedTabName"></span>
            <input type="hidden" id="hdnActionName" v-bind:value="Acctab" />
            <span class="p-actions float-right">
