                v-bind:class="{ 'disabled': (LeadStatus == 'Closed') }"
                title="@Resources.ConvertLead"
                v-on:click="ChangeCRMLeadOwnerCheck"
              >
                <em class="fa fa-retweet"></em>
                <br />
                {{ $t('Convert') }}
              </a>
              <a
                href="javascript:;"
