              >
                <em class="fa fa-plus"></em>
                <br />
                {{ $t('AssociateContact') }}
              </a>

              <a
                href="javascript:void(0)"
                v-bind:class="{ 'p-action-btn text-white btn_email': true }"
                title
                id
                v-on:click="openCRMEmailModal(null)"
                v-if="isSendEmail"
                data-original-title="Send Email"
              >
                <em class="fa fa-envelope"></em>
                <br />
                {{ $t('SendEmail') }}
              </a>
              <a
                href="javascript:void(0)"
                v-bind:class="{ 'p-action-btn text-white btn_add': true }"
