              >
                <em class="fa fa-phone"></em>
                <br />
                {{ $t('Schedule') }} {{ $t('Call') }}
              </a>
              <a
                href="javascript:;"
                v-bind:class="{ 'p-action-btn text-white': true, 'disabled': (LeadStatus == 'Closed') }"
