              >
                <em class="fa fa-plus"></em>
                <br />
                {{ $t('Add') }} {{ $t('Attachment') }}
              </a>
              <a
                href="javascript:;"
                class="p-action-btn text-white hr_jobpost_b"
                v-if="LeadStatus != 'Closed' && LeadStatus != 'Inactive' && LeadStatus != 'Draft' && LeadStatus != 'Cancelled'"
                v-bind:chkk="LeadStatus"
