                class="p-action-btn text-white"
                v-on:click="ReopenLead()"
              >
                <em class="fa fa-refresh"></em>
                <br />
                {{ $t('Reopen') }}
              </a>
            </span>
            <span class="collapse-head-btns">
              <a href="javascript:;" class="collapse-btns">
                <em class="fa fa-angle-up text-white"></em>
              </a>
            </span>
          </div>
        </div>
        <div class="tab-pane mb-0">
          <div class="tab-content fsmtabcontrolMobileView" id="myTabContent">
            <div class="expand-list">
              <a
                href="javascript:;"
                class="expand-list-btn justify-content-center align-items-center"
              >
                <em class="icon-solid arrow-solid-left text-white" style="font-size:9pt"></em>
              </a>
            </div>
            <div class="tab-pane p-0 pb-2 border-0"
              :class="{ 'active show': activeKey == 1 }"
              role="tabpanel"
              id="tab-lead-content"
              aria-labelledby="tab-lead-content"
            >
              <div class="listing">
                <div class="row">
                  <div class="col-md-12">
                    <div class="card mb-3 probx">
                      <div class="card-header">
                        <h2>{{ $t('Details') }}</h2>
                        <div class="cont_right">
                          <div class="bar"></div>
                        </div>
                      </div>
                      <div class="card-body">
                        <div class="panel1" v-for="group in LeftViewData">
                          <div class="heading-border">
                            <h5 class="h5 mb-2">
                              <span> {{ $t(group.group_name, '') }}</span>
                            </h5>
                          </div>
                          <div class="row">
                            <div
                              class="col-md-6 col-lg-3"
                              v-for="(item, index) in group.LeftViewData" :key="index"
                            >
                              <div class="form-group">
                                <label>
                                  <strong> {{ $t(item.name, item.display_name) }}:</strong>
                                  <span
                                    class="answer rightlabeltext ml-1"
                                    v-if="item.dt_code == 'date' || item.dt_code == 'datetime'"
                                  > {{item.value  }}</span>
                                  <span
                                    class="answer rightlabeltext ml-1"
                                    v-if="item.dt_code == 'decimal'"
                                  > {{isBlank(item.value)?"0.00":item.value | currency}}
                                  </span>
                                  <span
                                    class="answer rightlabeltext ml-1"
                                    v-if="item.dt_code != 'date' && item.dt_code != 'datetime' && item.dt_code != 'decimal' && item.picklist_options != 'Lookup'"
                                  > {{
                                    isBlank(item.value) ? "--" : (item.value)
                                    }}
                                  </span>
                                  <span
                                    class="answer rightlabeltext ml-1"
                                    v-if="item.dt_code != 'date' && item.dt_code != 'datetime' && item.dt_code != 'decimal' && item.picklist_options == 'Lookup'"
                                  > {{
                                    isBlank(item.value) ? "--" : (FilterDDLandNormalValue(item.value))
                                    }}
                                  </span>
                                </label>
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                    <div class="card mb-3 probx">
                      <div class="card-header">
                        <h2>Timeline</h2>
                        <div class="col-md-6 text-right pr-0 bottom_filter_button pt-0">
                          <a
                            href="javascript:;"
                            id="ancActivityExpandAllCollapseAll"
                            class="position-relative"
                            style="z-index:999;"
                            :title="$t('ExpandAllCollapseAll')"
                            data-toggle-tooltip="tooltip"
                          >{{ $t('ExpandAllCollapseAll') }}</a>
                        </div>
                      </div>
                      <div class="card-body">
                        <div class="row">
                          <div class="col-md-12 mbspc" id="divconversationbox">
                            <crmtimeline v-bind:timelinedata="timelinedata"></crmtimeline>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="tab-pane fade p-0 pb-2 border-0"
              :class="{ 'active show': activeKey == 2 }"
              id="tab-notes-content"
              role="tabpanel"
              aria-labelledby="tab-notes-content"
            >
              <crmnote
                v-if="noteData"
                v-bind:notedata="noteData"
                :userid="userid"
                :companyid="companyId"
                :sourceid="$route.params.id"
                :modulename="$route.params.moduleName"
                :submodulecode="$route.params.subModuleCode"
                v-bind:listtype="NoteListType"
                :showNotePopUp="isShowNotePopup"
                callfrom="quickViewAndNoteJS"
              ></crmnote>
            </div>
            <div
              class="tab-pane fade p-0 pb-2 border-0"
              :class="{ 'active show': activeKey == 3 }"
              id="tab-attachment-content"
              role="tabpanel"
              aria-labelledby="tab-attachment-content"
            >
              <crmattachment v-bind:attachmentdata="attachmentData" :sourceid="$route.params.id"></crmattachment>
            </div>
            <div
              class="tab-pane fade border-0 listing p-0"
              :class="{ 'active show': activeKey == 4 }"
              id="tab-openactivities-content"
              role="tabpanel"
              aria-labelledby="tab-openactivities-content"
            >
              <input id="hdnshowspliticon" value="1" type="hidden" />
              <div class="table-responsive" v-if="!noRecordForTab">
                <table
                  class="table table-bordered dt-responsive nowrap mt-0"
                  id="table-openactivities-content" aria-describedby="activitescontent"
                >
                  <thead class="thead-bg">
                    <tr>
                       <th scope="col">
                        <span>{{ $t('Subject') }}</span>
                      </th>
                       <th scope="col">
                        <span>{{ $t('Activity') }} {{ $t('Type') }}</span>
                      </th>
                       <th scope="col">
                        <span>{{ $t('Activity') }} {{ $t('ScheduleDate') }}</span>
                      </th>
                       <th scope="col">
                        <span>{{ $t('Activity') }} {{ $t('OWNER') }}</span>
                      </th>
                       <th scope="col">
                        <span>{{ $t('CreatedAt') }}</span>
                      </th>
                       <th scope="col">
                        <span>{{ $t('Status') }}</span>
                      </th>
                       <th scope="col" class="text-center">
                        <span>{{ $t('Action') }}</span>
                      </th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="item in activitiesData">
                      <td>
                        <a
                          href="javascript:;"
                          v-on:click="openActivityModal(item);"
                        >{{ item.followup_notification_subject }}</a>
                      </td>
                      <td>
                        <div v-if="item.activity_type === 'FollowUp'">Follow up</div>
                        <div v-else>{{ item.activity_type }}</div>
                      </td>
                      <td>{{item.followup_notification_schedule | formatDate}}</td>
                      <td>{{ item.activity_owner }}</td>
                      <td>{{item.created_at | formatDate}}</td>
                      <td>
                        <span
                          v-bind:class="{ 'px-2 bg-success text-white d-block text-center': (item.status_name == 'Active'), 'px-2 bg-danger text-white d-block text-center': (item.status_name == 'Inactive') }"
                        >{{ item.status_name }}</span>
                      </td>
                      <td class="text-center single-action">
                        <a href="javascript:;" title="Delete" v-on:click="DeleteActivity(item);">
                          <em class="fa fa-trash action-icon text-danger"></em>
                        </a>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
              <div
                v-if="noRecordForTab"
                class="text-center text-danger p-4 border no-record"
              >{{ $t('NoRecordfound') }}</div>
            </div>
            <div
              class="tab-pane fade border-0 p-0"
              :class="{ 'active show': activeKey == 5 }"
              id="tab-emails-content"
              role="tabpanel"
              aria-labelledby="tab-emails-content"
            >
              <crmsendemail v-bind:emaildata="EmailData" @GetEmailInfo="GetEmailInfo"></crmsendemail>
            </div>
            <div
              class="tab-pane fade p-0 border-0"
              :class="{ 'active show': activeKey == 6 }"
              id="tab-Contact-content"
              role="tabpanel"
              aria-labelledby="tab-Contact-content"
            >
              <crmcontacttab
                v-bind:contactsdata="contactsData"
                v-bind:contactsheader="contactsHeader"
                :sourceid="$route.params.id"
                :submodulecode="$route.params.subModuleCode"
                :modulename="$route.params.moduleName"
                :userinfo="userid"
                :companyid="companyId"
              ></crmcontacttab>
            </div>
            <div
              class="tab-pane fade p-0 border-0"
              :class="{ 'active show': activeKey == 7 }"
              role="tabpanel"
              aria-labelledby="tab-products-content"
              id="tab-products-content"
            >
              <crmproduct
                v-bind:productdata="productData"
                :sourceid="$route.params.id"
                :submodulecode="$route.params.subModuleCode"
                :modulename="$route.params.moduleName"
                :userinfo="userid"
                :companyid="companyId"
                v-bind:recordstatus="LeadStatus"
              ></crmproduct>
            </div>
            <div
              class="tab-pane fade border-0 p-0"
              :class="{ 'active show': activeKey == 8 }"
              role="tabpanel"
              aria-labelledby="tab-calls-content"
              id="tab-calls-content"
            >
              <crmcall
                v-bind:calldata="callData"
                :sourceid="$route.params.id"
                :userinfo="userid"
                :companyid="companyId"
              ></crmcall>
            </div>
            <div
              class="tab-pane fade p-0 listing border-0"
              :class="{ 'active show': activeKey == 9 }"
              role="tabpanel"
              aria-labelledby="tab-CoOwner-content"
              id="tab-CoOwner-content"
            >
              <div class="table-responsive" v-if="!noRecordForTab">
                <table
                  class="table table-bordered dt-responsive nowrap mt-0"
                  id="table-leadOwner-content" aria-describedby="leaddetail"
                >
                  <thead class="thead-bg">
                    <tr>
                       <th scope="col">
                        <span>{{ $t('LeadOwner') }}</span>
                      </th>
                       <th scope="col">
                        <span>{{ $t('Department') }}</span>
                      </th>
                       <th scope="col" class="text-center" data-width="50" style="min-width:100px!important;">
                        <span>{{ $t('Action') }}</span>
                      </th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="(item,index) in additionalLeadOwners" :key="index">
                      <td>{{ item.USER_NAME }}</td>
                      <td>{{ item.DEPARTMENT_NAME }}</td>
                      <td class="text-center single-action" v-if="!item.IS_DEFAULT">
                        <a
                          href="javascript:;"
                          :title="$t('Edit')"
                          v-on:click="openAddAdditionalOwner(item.LEAD_OWNER_ID)"
                          v-bind:class="{ 'disabled': (LeadStatus == 'Closed') }"
                        >
                          <em     class="fa fa-pencil text-success action-icon pr-2"
                            v-bind:class="{ 'disabled': (LeadStatus == 'Closed') }"
                          ></em>
                        </a>
                        <a
                          href="javascript:;"
                          :title="$t('Remove')"
                          v-on:click="deleteAdditionalOwner(item.LEAD_OWNER_ID)"
                          v-bind:class="{ 'disabled': (LeadStatus == 'Closed') }"
                        >
                          <em     class="fa fa-trash action-icon text-danger pr-2"
                            v-bind:class="{ 'disabled': (LeadStatus == 'Closed') }"
                          ></em>
                        </a>
                      </td>
                      <td
                        v-if="item.IS_DEFAULT"
                        class="text-center"
                      >{{ $t('Default') }} {{ $t('LeadOwner') }}</td>
                    </tr>
                  </tbody>
                </table>
              </div>
              <div
                v-if="noRecordForTab"
                class="text-center text-danger p-4 border no-record"
              >{{ $t('LeadOwner') }}</div>
            </div>
          </div>
        </div>
      </div>
    </section>
    <modalattachment
      v-if="showAttachmentModal"
      @close="showAttachmentModal = false"
      :userid="userid"
      :companyid="companyId"
      :id="$route.params.id"
      :modulename="$route.params.moduleName"
      :submodulecode="$route.params.subModuleCode"
      :containername="containername"
      imagesizedata
    >
      <h5 slot="header" class="modal-title">$t('Attachments')</h5>
    </modalattachment>
    <modaltask
      v-if="showAddTaskModal"
      @close="showAddTaskModal = false, modalTaskItem = null"
      :modalTaskItem="modalTaskItem"
    >
      <h5 slot="header" class="modal-title">{{ $t('AddTask') }}</h5>
    </modaltask>
    <modalevent
      v-if="showAddEventModal"
      @close="showAddEventModal = false, modalFollowUpItem = null"
      :modalFollowUpItem="modalFollowUpItem"
      v-bind:modulename="$route.params.moduleName"
      v-bind:submodulecode="$route.params.subModuleCode"
      :sourceid="$route.params.id"
    >
      <h5 slot="header" class="modal-title">{{ $t('FollowUp') }}</h5>
    </modalevent>
    <modalcall
      v-if="showCallModal"
      :modalCallItem="modalCallItem"
      @close="showCallModal = false, modalCallItem = null"
    >
      <h5 slot="header" class="modal-title">{{ $t('Schedule') }} {{ $t('Call') }}</h5>
    </modalcall>
    <modalemail
      v-if="sendEmailModal"
      @close="sendEmailModal = false"
      :email="email"
      :userid="userid"
      :companyid="companyId"
      :id="$route.params.id"
      :modulename="$route.params.moduleName"
      :submodulecode="$route.params.subModuleCode"
      :containername="containername"
      :EmailInfo="EmailInfo"
    >
      <h5 slot="header" class="modal-title">{{ $t("Email") }}</h5>
    </modalemail>
    <modalexistingcontact
      v-if="showExistingContactModal"
      :sourceid="$route.params.id"
      :submodulecode="$route.params.subModuleCode"
      :modulename="$route.params.moduleName"
      :userinfo="userid"
      :companyid="companyId"
      @close="showExistingContactModal = false"
      sourcename="Lead"
    >
      <h5 slot="header" class="modal-title">{{ $t('Contacts') }}</h5>
    </modalexistingcontact>
    <modalcontact
      v-if="showContactModal"
      @close="showContactModal = false"
      :sourceid="$route.params.id"
      :submodulecode="$route.params.subModuleCode"
      :modulename="$route.params.moduleName"
      :userinfo="userid"
      :iscontactDetails="iscontactDetails"
      :companyid="companyId"
      sourcename="Lead"
    ></modalcontact>
    <addadditionalowner v-if="showAdditionalOwnerPopup" subModuleCode="CRM_LEADS" v-on:close="showAdditionalOwnerPopup = false"></addadditionalowner>
    <modalproduct
      v-if="showProductModal"
      :sourceid="$route.params.id"
      :submodulecode="$route.params.subModuleCode"
      :modulename="$route.params.moduleName"
      userinfo="userid"
      companyid="companyId"
      @close="showProductModal = false"
    >
      <h5 slot="header" class="modal-title">{{$t("Products")}}</h5>
    </modalproduct>  
    <modalconvertlead :leadId="$route.params.id" :moduleName="$route.params.moduleName" :subModuleCode="$route.params.subModuleCode" callFrom="LeadDetail" v-if="showConvertLead" @close="closeConvertLead" ></modalconvertlead> 
  </div>
</template>
